import { HeaderComponent } from "../components/header";
import Link from "next/link";
import catalogs from "../styles/catalogs.module.scss";
import {Cards} from "../components/Cards";
const Catalogs = () => {
  return (
    <body>
      <div>
        <HeaderComponent />
      </div>
      <div className={catalogs.navigateSectionClotch}>
        <Link href="#menClotch" className={catalogs.linkNavigate}>Men </Link>
        <Link href="#womenClotch" className={catalogs.linkNavigate}>Women</Link>
        <Link href="#kidsClotch" className={catalogs.linkNavigate}>Kids</Link>
      </div>
      <div id="menClotch" className={catalogs.menClotch}>
        <p>Men clotch</p>
        <Cards />
      </div>
      <div id="womenClotch">
        <p>Women clotch</p>
      </div>
      <div id="kidsClotch">
        <p>Kids clotch</p>
      </div>
    </body>
  );
};

export default Catalogs;
